import { login } from '../../api/auth';
import { useState, useEffect, useContext, createContext } from 'react';
import { useLocation } from 'react-router-dom';
import * as jwt from 'jsonwebtoken';
import { set } from 'react-hook-form';

const defaultAuthContext = {
  isAuthentic: false,
  currentMember: null,
  login: null,
  logout: null,
  register: null,
};

const AuthContext = createContext(defaultAuthContext);

export const useAuth = () => useContext(AuthContext);
export const AuthProvider = ({ children }) => {
  const [isAuthentic, setIsAuthentic] = useState(null);
  const [payload, setPayload] = useState(null);
  //
  const { pathname } = useLocation();
  const [modalReplyOpen, setModalReplyOpen] = useState(false);
  const [tweetId, setTweetId] = useState(null);
  const [member, setMember] = useState({});

  // token
  useEffect(() => {
    const checkTokenIsValid = async () => {
      const authToken = localStorage.getItem('authToken');
      if (!authToken) {
        setIsAuthentic(false);
        setPayload(null);
        return;
      }
      // @ checkPermission 
      // const result = await checkPermission(authToken);
      // if (result) {
      //   setisAuthentic(true);
      //   const tempPayload = jwt.decode(authToken);
      //   setPayload(tempPayload);
      // } else {
      //   setisAuthentic(false);
      //   setPayload(null);
      // }

      //@ checkPermission token+token
      if (authToken) {
        setIsAuthentic(true);
        const tempPayload = jwt.decode(authToken);
        setPayload(tempPayload);
        setMember(tempPayload);
        return;
      }
    };
    checkTokenIsValid();
  }, [pathname]);

  return (
    <AuthContext.Provider
      value={{
        isAuthentic,
        currentMember: payload && {
          id: payload.id,
          name: payload.name,
          avatar: payload.avatar,
        },
        setModalReplyOpen,
        modalReplyOpen,
        setTweetId,
        tweetId,
        member,
        login: async (user) => {
          console.log('ok');
          const { success, data } = await login({
            account: user.account,
            password: user.password,
          });
          const token = data.token;
          const tempPayload = jwt.decode(token);
          // console.log('data',data)
          // console.log('tempPayload: ', tempPayload )
          //{id: 14, account: 'user1', email: 'user1@example.com', name: 'user1 name', avatar: null,}
          if (tempPayload) {
            setIsAuthentic(true);
            localStorage.setItem('authToken', token);
            setPayload(tempPayload);
            //test-token CheckPermission 
            setMember(tempPayload);
          } else {
            setIsAuthentic(false);
          }
          return success;
        },
        logout: async () => {
          localStorage.removeItem('authToken');
          setIsAuthentic(false);
          setPayload(null);
        },
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};
